import React from "react"
import { PageProps } from "gatsby"
import { StandardLayout } from "../components/layout/standard-layout"
import { Container } from "reactstrap"
import { PageHeader } from "../components/layout/page-header"
import { useViewportQuery } from "../util"

const ResponsiveGoogleCalendar = () => {
  const atLeast = useViewportQuery("at least")
  return atLeast.sm ? (
    <iframe
      src="https://calendar.google.com/calendar/embed?height=600&amp;wkst=1&amp;bgcolor=%23ffffff&amp;ctz=America%2FLos_Angeles&amp;src=ZzkwaG42anZ2NTRxdGFiNWVnN2ZiZDJjNGNAZ3JvdXAuY2FsZW5kYXIuZ29vZ2xlLmNvbQ&amp;color=%23EF6C00&amp;showCalendars=0"
      style={{ width: "100%", border: "solid 1px #777" }}
      height="600"
      scrolling="no"
    ></iframe>
  ) : (
    <iframe
      src="https://calendar.google.com/calendar/embed?height=600&amp;wkst=1&amp;bgcolor=%23ffffff&amp;ctz=America%2FLos_Angeles&amp;src=ZzkwaG42anZ2NTRxdGFiNWVnN2ZiZDJjNGNAZ3JvdXAuY2FsZW5kYXIuZ29vZ2xlLmNvbQ&amp;color=%23EF6C00&amp;showCalendars=0&amp;showTabs=0&amp;mode=AGENDA&amp;showNav=0&amp;showDate=1&amp;showPrint=1"
      style={{ width: "100%", border: "solid 1px #777" }}
      width="400"
      height="600"
      scrolling="no"
    ></iframe>
  )
}
export default ({}: PageProps) => {
  return (
    <StandardLayout>
      <Container>
        <PageHeader pageTitle="Events" />
        <ResponsiveGoogleCalendar />
      </Container>
    </StandardLayout>
  )
}
